import React, { useEffect, useState } from "react";
import Button from "../../components/Button/Button";
import "./Lesson.css";
import Textbox from "../../components/Textbox/Textbox";
import Loading from "../../components/Loading/Loading";
import {
  collection,
  query,
  getDocs,
  doc,
  updateDoc,
  addDoc,
} from "firebase/firestore";
import { firestore } from "../../firebase";
import Select from "react-select";

const AppUpdate = () => {
  const [textData, setTextData] = useState({
    category: "",
    name: "",
    thumbnail: "",
    isLocked: false,
    loading: true,
    options: [],
  });

  async function getCollection() {
    const collectionName = collection(firestore, "category");
    const q = query(collectionName);
    const querySnapshot = await getDocs(q);
    querySnapshot.forEach((doc) => {
      // console.log(doc.data().totalLessons);
      setTextData((prevData) => {
        return {
          ...prevData,
          options: [
            ...prevData.options,
            {
              value: doc.id,
              label: doc.data().name,
              key: doc.id,
              totalVideos: doc.data().totalLessons,
            },
          ],
          loading: false,
        };
      });
    });

    return q;
  }

  useEffect(() => {
    getCollection();
  }, []);

  async function submit(e) {
    if (
      textData.category !== "" &&
      textData.name !== "" &&
      textData.thumbnail !== ""
    ) {
      setTextData((prevData) => {
        return {
          ...prevData,
          loading: true,
        };
      });
      // console.log(textData.category);

      try {
        await addDoc(collection(firestore, "lessons"), {
          category: textData.category.key,
          name: textData.name,
          thumbnail: textData.thumbnail,
          isLocked: textData.isLocked,
          totalVideos: 0,
        });

        try {
          const collectionName = doc(
            firestore,
            "category",
            textData.category.key
          );
          await updateDoc(collectionName, {
            totalLessons: textData.category.totalVideos + 1,
          });
        } catch (e) {
          console.log("Error adding document: ", e);
        }
      } catch (e) {
        alert("Error adding document: ", e);
      }

      getCollection();
      setTextData(() => {
        return {
          options: [],
          category: "",
          name: "",
          thumbnail: "",
          isLocked: false,
          loading: false,
        };
      });
    } else alert("Please enter all details");
  }

  function doCheck() {
    setTextData((prevData) => {
      return {
        ...prevData,
        isLocked: !prevData.isLocked,
      };
    });
  }

  const textHand = (e) => {
    setTextData((prevData) => {
      return {
        ...prevData,
        [e.target.name]: e.target.value,
      };
    });
  };

  const customStyle = {
    container: (provided) => ({
      ...provided,
      // border: "solid",
      height: "40px",
      padding: "3px",
      paddingLeft: "10px",
      borderRadius: "10px",
      fontSize: "16px",
      width: "70vw",
      maxWidth: "280px",
      margin: "10px",
    }),
    menu: (provided, state) => ({
      ...provided,
      width: state.selectProps.width,
      borderBottom: "1px dotted pink",
      color: state.selectProps.menuColor,
      padding: 20,
    }),

    control: (provided, state) => ({
      ...provided,
      height: "40px",
      padding: "0",
      paddingLeft: "10px",
      borderRadius: "10px",

      fontSize: "16px",
      borderColor: state.isFocused ? "black" : "grey",
    }),

    singleValue: (provided, state) => {
      const opacity = state.isDisabled ? 0.5 : 1;
      const transition = "opacity 300ms";

      return {
        ...provided,
        width: "70vw",
        maxWidth: "280px",
        opacity,
        transition,
      };
    },
  };

  return (
    <div className="primary-container">
      <Loading title="Loading..." visible={textData.loading} />

      <Select
        placeholder="Select Category"
        styles={customStyle}
        value={textData.category}
        options={textData.options}
        onChange={(e) => {
          setTextData((prevData) => {
            return { ...prevData, category: e };
          });
        }}
      />

      <Textbox
        onChange={textHand}
        value={textData.name}
        placeholder="Lesson Name"
        type="text"
        name="name"
      />

      <Textbox
        onChange={textHand}
        value={textData.thumbnail}
        placeholder="Thumbnail URL"
        type="text"
        name="thumbnail"
      />
      <span className="container">
        <input
          checked={textData.isLocked}
          className="checkbox"
          type="checkbox"
          onChange={doCheck}
          name="isLocked"
          id=""
        />
        <span onClick={doCheck}>Premium</span>
      </span>
      <br />
      <Button onClick={submit} text="Submit" />
    </div>
  );
};

export default AppUpdate;
