import React, { useEffect, useState } from "react";
import Select from "react-select";
import Textbox from "../../components/Textbox/Textbox";
import Button from "../../components/Button/Button";
import Loading from "../../components/Loading/Loading";
import {
  collection,
  query,
  getDocs,
  doc,
  where,
  updateDoc,
  addDoc,
} from "firebase/firestore";
import { firestore } from "../../firebase";

const Video = () => {
  const [textData, setTextData] = useState({
    category: "",
    lesson: "",
    refrenceText: "",
    sortingOrder: "",
    timePalse: "",
    videoUrl: "",
    loading: true,
    options: [],
    LessonsOption: [],
  });

  async function getCollection() {
    const collectionName = collection(firestore, "category");
    const q = query(collectionName);
    const querySnapshot = await getDocs(q);
    querySnapshot.forEach((doc) => {
      // console.log(doc.data().totalLessons);
      setTextData((prevData) => {
        return {
          ...prevData,
          options: [
            ...prevData.options,
            {
              value: doc.id,
              label: doc.data().name,
              key: doc.id,
              totalVideos: doc.data().totalLessons,
            },
          ],
          loading: false,
        };
      });
    });
  }
  async function getLessions(categoryID) {
    //  console.log(categoryID);
    const lessonName = collection(firestore, "lessons");
    const q = query(lessonName, where("category", "==", categoryID));
    const querySnapshot = await getDocs(q);
    //  console.log(querySnapshot);

    if (!querySnapshot.empty) {
      querySnapshot.forEach((doc) => {
        setTextData((prevData) => {
          return {
            ...prevData,
            LessonsOption: [
              ...prevData.LessonsOption,
              {
                value: doc.id,
                label: doc.data().name,
                key: doc.id,
                totalVideos: doc.data().totalVideos,
              },
            ],
            loading: false,
          };
        });
      });
    } else {
      setTextData((prevData) => {
        return {
          ...prevData,
          loading: false,
        };
      });
      alert("No Lessons Found");
    }
  }

  useEffect(() => {
    getCollection();
  }, []);

  const textHand = (e) => {
    setTextData((prevData) => {
      return {
        ...prevData,
        [e.target.name]: e.target.value,
      };
    });
  };

  function onCategorySelection(category) {
    setTextData((prevData) => {
      return {
        ...prevData,
        LessonsOption: [],
        category: category,
        loading: false,
      };
    });

    getLessions(category.value);
  }

  async function submit() {
    if (
      textData.category === "" ||
      textData.lesson === "" ||
      textData.refrenceText === "" ||
      textData.sortingOrder === "" ||
      textData.timePalse === "" ||
      textData.videoUrl === ""
    ) {
      alert("Please fill all the fields");
    } else {
      const data = {
        category: textData.category.value,
        lesson: textData.lesson.value,
        refrenceText: textData.refrenceText,
        sortingOrder: parseInt(textData.sortingOrder),
        timePalse: parseFloat(textData.timePalse),
        videoUrl: textData.videoUrl,
      };

      setTextData((prevData) => {
        return {
          ...prevData,
          loading: true,
        };
      });

      try {
        await addDoc(collection(firestore, "videos"), data);

        try {
          const collectionName = doc(firestore, "lessons", textData.lesson.key);
          await updateDoc(collectionName, {
            totalLessons: textData.lesson.totalVideos + 1,
          });
        } catch (e) {
          console.log("Error adding document: ", e);
        }
      } catch (e) {
        alert("Error adding document: ", e);
      }

      setTextData({
        category: "",
        lesson: "",
        refrenceText: "",
        sortingOrder: "",
        timePalse: "",
        videoUrl: "",
        loading: true,
        options: [],
        LessonsOption: [],
      });

      getCollection();
      alert("Video Added Successfully");
    }
  }

  const customStyle = {
    container: (provided) => ({
      ...provided,
      height: "40px",
      padding: "3px",
      paddingLeft: "10px",
      borderRadius: "10px",
      fontSize: "16px",
      width: "70vw",
      maxWidth: "280px",
      margin: "10px",
    }),
    menu: (provided, state) => ({
      ...provided,
      width: state.selectProps.width,
      borderBottom: "1px dotted pink",
      color: state.selectProps.menuColor,
      padding: 20,
    }),

    control: (provided, state) => ({
      ...provided,
      height: "40px",
      padding: "0",
      paddingLeft: "10px",
      borderRadius: "10px",

      fontSize: "16px",
      borderColor: state.isFocused ? "black" : "grey",
    }),

    singleValue: (provided, state) => {
      const opacity = state.isDisabled ? 0.5 : 1;
      const transition = "opacity 300ms";

      return {
        ...provided,
        width: "70vw",
        maxWidth: "280px",
        opacity,
        transition,
      };
    },
  };

  return (
    <div className="primary-container">
      <Loading title="Loading..." visible={textData.loading} />

      <Select
        placeholder="Select Category"
        styles={customStyle}
        value={textData.category}
        options={textData.options}
        onChange={(e) => {
          onCategorySelection(e);
        }}
      />
      <Select
        placeholder="Select Lesson"
        styles={customStyle}
        value={textData.lesson}
        options={textData.LessonsOption}
        onChange={(e) => {
          setTextData((prevData) => {
            return { ...prevData, lesson: e };
          });
        }}
      />

      <Textbox
        onChange={textHand}
        value={textData.refrenceText}
        placeholder="Refrence Text"
        type="text"
        name="refrenceText"
      />

      <Textbox
        onChange={textHand}
        value={textData.sortingOrder}
        placeholder="Sorting Order"
        type="number"
        name="sortingOrder"
      />
      <Textbox
        onChange={textHand}
        value={textData.timePalse}
        placeholder="Time Palse"
        type="number"
        name="timePalse"
      />

      <Textbox
        onChange={textHand}
        value={textData.videoUrl}
        placeholder="Video URL"
        type="text"
        name="videoUrl"
      />

      <br />
      <Button onClick={submit} text="Submit" />
    </div>
  );
};

export default Video;
