import React, { useState } from "react";
import Textbox from "../../components/Textbox/Textbox";
import Button from "../../components/Button/Button";
import Loading from "../../components/Loading/Loading";
import { firestore } from "../../firebase";
import { collection, addDoc } from "firebase/firestore";

const CreateLession = () => {
  const [textData, setTextData] = useState({
    logoI: "",
    lessionN: "",
    loading: false,
  });

  const textHand = (e) => {
    setTextData((prevData) => {
      return {
        ...prevData,
        [e.target.name]: e.target.value,
      };
    });
  };

  const submit = async () => {
    if (textData.lessionN && textData.logoI) {
      setTextData((prevData) => {
        return {
          ...prevData,
          loading: true,
        };
      });

      try {
        await addDoc(collection(firestore, "category"), {
          name: textData.lessionN,
          thumbnail: textData.logoI,
          totalLessons: 0,
        });
      } catch (e) {
        alert("Error adding document: ", e);
      }

      setTextData({
        logoI: "",
        lessionN: "",
      });

      setTextData((prevData) => {
        return {
          ...prevData,
          loading: false,
        };
      });
    } else {
      // console.log("check");

      alert("Enter all details");
    }
  };

  return (
    <div className="primary-container">
      <Loading title="Loading..." visible={textData.loading} />
      <Textbox
        onChange={textHand}
        value={textData.lessionN}
        placeholder="Category Name"
        type="text"
        name="lessionN"
      />
      <Textbox
        onChange={textHand}
        value={textData.logoI}
        placeholder="Thumbnail URL"
        type="text"
        name="logoI"
      />
      {/* <Textbox onChange={textHand} value={textData.cTime} placeholder="Completion Time" type="text" name="cTime" />
            <Textbox onChange={textHand} value={textData.quizMSG1} placeholder="Quiz MSG 1" type="text" name="quizMSG1" />
            <Textbox onChange={textHand} value={textData.quizMSG2} placeholder="Quiz MSG 2" type="text" name="quizMSG2" />
            <Textbox onChange={textHand} value={textData.quizMSG3} placeholder="Quiz MSG 3" type="text" name="quizMSG3" />
            <Textbox onChange={textHand} value={textData.quizMSG4} placeholder="Quiz MSG 4" type="text" name="quizMSG4" /> */}
      <br /> <Button text="Submit" onClick={submit} />
    </div>
  );
};

export default CreateLession;
