import { BrowserRouter, Switch, Route } from "react-router-dom";
import "./App.css";
import { AuthProvider, useAuth } from "./contexts/AuthContext";
import Signin from "./components/Signin/Signin";
import { useEffect, useState } from "react";
import Navigation from "./components/Navigation/Navigation";
import Home from "./pages/Home/Home";
import CreateLession from "./pages/Category/Category";
import AppUpdate from "./pages/Lesson/Lesson";
import Video from "./pages/Video/Video";
import { firestore } from "./firebase";
import { collection, query, getDocs, where } from "firebase/firestore";

const App = () => {
  return (
    <BrowserRouter>
      <Navigation />
      <AuthProvider>
        <Switch>
          <GuestRoute path="/" exact>
            <Signin />
          </GuestRoute>

          <ProtectedRoute path="/category">
            <CreateLession />
          </ProtectedRoute>

          <ProtectedRoute path="/lessons">
            <div className="primary-container">
              <AppUpdate />
            </div>
          </ProtectedRoute>

          <ProtectedRoute path="/quiz">
            <div className="primary-container">
              <h1>Quiz </h1>
            </div>
          </ProtectedRoute>

          <ProtectedRoute path="/video">
            <div className="primary-container">
              <Video />
            </div>
          </ProtectedRoute>
          <Route>
            <div className="primary-container">
              <h1>404 Page Not Found </h1>
            </div>
          </Route>
        </Switch>
      </AuthProvider>
    </BrowserRouter>
  );
};

const GuestRoute = ({ children, ...rest }) => {
  const { currentUser } = useAuth();
  const [valid, setValid] = useState(true);

  useEffect(async () => {
    if (currentUser) {
      const collectionName = collection(firestore, "security");
      const q = query(collectionName, where("email", "==", currentUser.email));
      const querySnapshot = await getDocs(q);

      !querySnapshot.empty ? setValid(true) : setValid(valid);

      // const path = ref(adminFB, "Security");
      // onValue(path, (snapshot) => {
      //   snapshot.forEach(function (child) {
      //     if (currentUser.email.replace(".", "%") === child.key) {
      //       setValid(true);
      //     }
      //   });
      // });
    }
  }, [currentUser]);

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return currentUser && valid ? <Home /> : children;
      }}
    ></Route>
  );
};

const ProtectedRoute = ({ children, ...rest }) => {
  const { currentUser } = useAuth();
  const [valid, setValid] = useState(false);

  useEffect(async () => {
    const collectionName = collection(firestore, "security");
    const q = query(collectionName, where("email", "==", currentUser.email));
    const querySnapshot = await getDocs(q);

    !querySnapshot.empty ? setValid(true) : setValid(valid);

    // const path = ref(adminFB, "Security");
    // onValue(path, (snapshot) => {
    //   snapshot.forEach(function (child) {
    //     if (currentUser.email.replace(".", "%") === child.key) {
    //       setValid(true);
    //     }
    //   });
    // });
  }, [currentUser]);

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return !currentUser || !valid ? <Signin /> : children;
      }}
    ></Route>
  );
};

export default App;
